<template>
  <div>
    <v-modal :visible="modalVisible" @hide="modalVisible = false">
      <template v-slot:title>«Возможно, Вами допущена ошибка»</template>
      <template v-slot:text>
        Пожалуйста, проверьте введенные Вами данные. Если данные введены
        корректно, и проверка персональных данных не проходит, просим обратиться
        в офис Фонда. <v-link>Контакты и офисы АО «НПФ»</v-link> 
      </template>
    </v-modal>

    <step-30-70 class="step step-1" :stepNum="1" :amountSteps="7">

      <template v-slot:button-next>
          <button class="v-button" @click="next">Далее</button>
      </template>
      <template v-slot:button-prev>
          <button class="v-button" @click="prev">Назад</button>
      </template>

      <template v-slot:title>Первый шаг</template>

      <template v-slot:col1-content>
        <tit-h3 class="mb-4"> Основные данные </tit-h3>
        <div class="form-inputs__row order-md-0 order-1 input-wrapper ">
          <v-input placeholder="Фамилия"></v-input>
        </div>
        <div class="form-inputs__row order-md-1 order-2 input-wrapper">
          <v-input placeholder="Имя"></v-input>
        </div>
        <div
          class="
            form-inputs__row
            order-md-2 order-3
            row
            align-items-center
            ms-0
            me-0
          "
        >
          <v-input
            ref="patronymicInput"
            placeholder="Отчество"
            class="col-md-8 col-8"
          ></v-input>
          <span class="col-md-4 col-4 _no text-end d-flex justify-content-end  m-0 p-0">
            <custom-input type="checkbox" class="me-10px">
              <input type="checkbox" />
            </custom-input>
            <span  class="pt-2px">
              Нет
            </span>
            
          </span>
        </div>
        <div class="form-inputs__row order-md-3 order-0 d-flex gap-2">
          <label for="sex-man" class="col-6 d-flex">
            <custom-input class="me-10px">
              <input type="radio" checked name="sex" id="sex-man" @change="sex = 'man'" />
            </custom-input>
            <span :class="{'disabled': sex === 'woman'}" class="pt-2px sex"> Мужчина </span>
          </label>
          <label for="sex-woman" class="col-6 d-flex justify-content-end">
            <custom-input class="me-10px">
              <input type="radio" name="sex" id="sex-woman" @change="sex = 'woman'"/>
            </custom-input>
            <span :class="{'disabled': sex === 'man'}" class="pt-2px sex"> Женщина </span>
          </label>
        </div>
      </template>

      <template v-slot:col2-content>
        <tit-h3 class="mb-4"> Паспортные данные, СНИЛС </tit-h3>
        <b-row class="flex-wrap inputs">
          <div class="col-md-3 col-6 order-0 input-wrapper">
            <v-input placeholder="СНИЛС"></v-input>
          </div>
          <div class="col-md-3 col-6 order-1 input-wrapper">
            <v-input placeholder="Дата рождения"></v-input>
          </div>
          <div class="col-md-6 col-12 order-2 input-wrapper">
            <v-input placeholder="Место рождения"></v-input>
          </div>
          <div class="col-md-3 col-6 order-3 input-wrapper">
            <v-input placeholder="Серия паспорта"></v-input>
          </div>
          <div class="col-md-3 col-6 order-4 input-wrapper">
            <v-input placeholder="Номер паспорта"></v-input>
          </div>
          <div class="col-md-3 col-6 order-6 order-md-5 input-wrapper">
            <v-input placeholder="Дата выдачи"></v-input>
          </div>
          <div class="col-md-3 col-6 order-7 order-md-6 input-wrapper">
            <v-input placeholder="Код подразделения"></v-input>
          </div>
          <div
            :class="{ 'order-5': pageWidth < 768 }"
            class="col-12 order-md-7 input-wrapper"
          >
            <v-input placeholder="Кем выдан"></v-input>
          </div>
        </b-row>

        <label for="dfivm4" class="mt-4">
          <custom-input class="me-md-3">
            <input type="checkbox" id="dfivm4" />
          </custom-input>
          Я даю своё согласие на
          <a href="" class="btn-link">обработку персональных данных</a>
        </label>
      </template>

      
    </step-30-70>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pageWidth: window.innerWidth,
      modalVisible: true,
      sex: 'man',
    };
  },
  methods: {
    next() {
      this.$emit('next')
    },
    prev() {
      this.$emit('prev')
    }
  },
};
</script>

<style lang="scss" csoped>
.sex.disabled {
  color: rgba(0,0,0, .5);
}
.me-10px {
  margin-right: 10px;
}
.pt-2px {
  padding-top: 2px;
}
.inputs {
  row-gap: 30px;

  @media (max-width:768px) {
    row-gap: 20px;
  }
}
</style>