<template>
  <step-50-50 class="step step-5" :stepNum="5" :amountSteps="7">
    <template v-slot:button-next>
      <button class="v-button" @click="next">Далее</button>
    </template>
    <template v-slot:button-prev>
      <button class="v-button" @click="prev">Назад</button>
    </template>

    <template v-slot:title> Пятый шаг </template>

    <template v-slot:col1-content>
      <tit-h3> Подтвердите номер мобильного телефона </tit-h3>
      <v-text class="">
        SMS-код для подтверждения Вашего мобильного телефонабыл направлен на
        указанный Вами номер:
      </v-text>

      <div>
        <!-- ИНПУТ ДЛЯ ИЗМЕНИНИЯ ТЕЛЕФОНА -->
        <v-input
          class="update-input col-6 col-lg-7"
          value="+7 (916) 321-45-12"
          :disabled="!upadateTelInput"
          ref="updateTel"
        >
        </v-input>
        <button
          class="v-link d-inline-block col-6 col-lg-5 text-end update-info"
          @click="upadateTelInput = upadateTelInput ? false : true"
        >
          <span v-if="!upadateTelInput">Изменить</span>
          <span v-else>Сохранить</span>
          телефон
        </button>
      </div>
      <b-row class="m-0">
        <v-input
          placeholder="Ввести SMS-код"
          class="col-6 col-lg-7 input-confirmation"
        >
        </v-input>
        <div class="col-6 col-lg-5 d-flex justify-content-end p-0">
          <v-button class="_add col-md-11 col-10 confirmation-btn" disabled>
            Подтвердить
          </v-button>
        </div>
      </b-row>
      <v-text class="mb-0"> Выслать новый SMS-код через: </v-text>

      <div>
        <v-text v-if="telTimer" class="fw-bold m-0 step__timer">{{telTimer}} секунд...</v-text>

        <button v-else class="v-link text-start send-code" @click="sendTelCode">
          Отправить код повторно
        </button>
      </div>
    </template>

    <template v-slot:col2-content>
      <tit-h3> Подтвердите E-mail адрес </tit-h3>
      <v-text>
        Email-код для подтверждения Вашей электронной почтыбыл направлен на
        указанный Вами E-mail адрес:
      </v-text>

      <div>
        <!-- ИНПУТ ДЛЯ ИЗМЕНИНИЯ EMAIL -->
        <v-input
          class="update-input col-6 col-lg-7"
          value="panov.ivan@mail.ru"
          :disabled="!upadateEmailInput"
          ref="updateTel"
        >
        </v-input>
        <button
          class="v-link d-inline-block col-6 col-lg-5 text-end update-info"
          @click="upadateEmailInput = upadateEmailInput ? false : true"
        >
          <span v-if="!upadateEmailInput">Изменить</span>
          <span v-else>Сохранить</span>
          почту
        </button>
      </div>
      <b-row class="m-0">
        <v-input
          placeholder="Ввести Email-код"
          class="col-6 col-lg-7 input-confirmation"
        >
        </v-input>
        <div class="col-6 col-lg-5 d-flex justify-content-end p-0">
          <v-button class="_add col-md-11 col-10 confirmation-btn"> Подтвердить </v-button>
        </div>
      </b-row>
      <v-text class="mb-0"> Выслать новый Email-код через: </v-text>
      
      <div>
        <v-text v-if="emailTimer" class="fw-bold m-0 step__timer">{{emailTimer}} секунд...</v-text>

        <button v-else class="v-link text-start send-code" @click="sendEmailCode">
          Отправить код повторно
        </button>
      </div>
    </template>
  </step-50-50>
</template>

<script>
export default {
  data() {
    return {
      upadateTelInput: false,
      upadateEmailInput: false,
      pageWidth: window.innerWidth,
      againTelCode: false,
      againEmailCode: false,
      telTimer: 0,
      emailTimer: 0,
    };
  },
  methods: {
    startTelTimer() {
      let timer = setInterval(() => {
        if (this.telTimer > 0) {
          this.telTimer--
        } else {
          clearInterval(timer)
        }
      }, 1000)
    },
    startEmailTimer() {
      let timer = setInterval(() => {
        if (this.emailTimer > 0) {
          this.emailTimer--
        } else {
          clearInterval(timer)
        }
      }, 1000)
    },
    sendTelCode() {
      this.againTelCode = true;
      this.telTimer = 3;
      this.startTelTimer() 
    },
    sendEmailCode() {
      this.againEmailCode = true;
      this.emailTimer = 3;
      this.startEmailTimer() 
    },
    next() {
      this.$emit("next");
    },
    prev() {
      this.$emit("prev");
    },
  },
};
</script>

<style lang="scss">
.update-info {
  @media (max-width: 768px) {
    color: rgba(0, 0, 0, 0.5) !important;
  }
  
}
.step__timer {
  @media (max-width: 768px) {
    color: rgba(0, 0, 0, 0.5) !important;
  }
}
.update-info, .send-code {
  font-size: 14px;

  
  @media (max-width:567px) {
    font-size: 12px;
  }
}
.step-5 {
  .tit-h3 {
    margin: 0;
  }
  .confirmation-btn {
    padding: 0;
  }
  .update-input:disabled {
    background: none;
    border: solid 1px rgba(0,0,0,0);
    font-size: 14px;
    line-height: 18px;
    padding-left: 0;
    font-weight: 700;
    font-family: "Inter";
    color: rgba(0, 0, 0, 0.7);

    @media (max-width: 567px) {
      font-size: 12px;
    }
  }
  .update-input {
    transition: .1s;
    font-size: 14px;
    line-height: 18px;
    font-weight: 700;
    font-family: "Inter";
    color: rgba(0, 0, 0, 0.7);
    @media (max-width: 567px) {
      font-size: 12px;
    }
  }
  .input-confirmation.unvalide {
    background: #f6f8f9;
    border: 1px solid #c4c4c4;
  }

  .input-confirmation.unvalide::placeholder {
    color: #de231b;
    font-weight: 500;
  }
}
</style>